import styled from 'styled-components';

export const H1Style = styled.h1`
    font-size: 40px;
    text-align: center;
    font-family: 'Roboto';
    font-weight: bold;
    word-wrap: wrap;
   
    @media (max-width: 350px) { 
        font-size: 30px;
    }
`;

export const CardsStyle = styled.div`
    background-color: #272b33;
    font-family: 'Roboto';
    color: white;
    padding: 3%;
    display: flex;
    align-items:center;
    justify-content: center;
    gap: 15px;

    @media (max-width: 600px) { 
        flex-direction: column;
        text-align: center;
    }
`;


export const TitleCardStyle = styled.div`
    border-radius: 8px;
    background-color: #3c3e44;
    padding: 1px 15px;
    color: #ff9800;
    
    @media (max-width: 600px) { 
        font-size: 14px;
    }
`;

export const ImageCardStyle = styled.img`
    border-radius: 8px;
    width: 335px;
`;

export const InfoCardStyle = styled.div`
    background-color: #3c3e44;
    border-radius: 8px;
    padding: 0 15px;
    display: flex;
    gap: 50px;
    
    @media (max-width: 576px) { 
        
    }
    
`;

export const PStyle = styled.p`
    color: ${props => props.$label ? "#9E9E9E" : "white"};
    font-size: ${props => props.$label ? "14px" : "16px"};
`;

export const TitleStyle = styled(PStyle)`
    text-align: center;
    font-size: 20px;
    width: 100%;
`;


export const ButtonStyle = styled.button`
    border-radius: 50%;
    background-color: #ff9800;
    border: none;
    height: 50px;
    width: 50px;
    cursor: pointer;
    font-size: 30px;
    position: fixed;
    top: ${props => props.$history ? "200px" : "120px"};
    right: 30px;
    font-weight: bold;
    color: #FAFAFA;
    z-index: 1;
    
    transform: translateY(0);
    transition: .3s ease;

    &:hover {
        transform:  translateY(-6px);
    }
`;

export const ModalStyle = styled.div`
    font-family: 'Roboto';
    font-size: 11px;
    background-color: rgba(32, 35, 41,0.92);
    padding: 15px;
    position: fixed;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    gap: 25px;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
`;


export const HistoryCardStyle = styled.div`
    display: flex;
    gap: 10px;
    align-items: center;
`;


export const ImageCardHistoryStyle = styled(ImageCardStyle)`
    width: 80px;
    height: 80px;
    border-radius: 8px;
`;

export const ButtonQueryStyle = styled.button`
    cursor: pointer;
    background-color: transparent;
    border: none;

    transform: translateY(0);
    transition: .3s ease;

    &:hover {
        transform:  translateY(-6px);
    }
`;