import { ButtonStyle } from '../StyleComponents';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import { MyContext } from '../MyContext';
import React from 'react';

function ButtonNew({ getNumberRandom }) {
    const {
        setIdGenerated,
        setDisplayCard, 
        setDisplayQuery,
        displayCard,
        displayQuery,
      } = React.useContext(MyContext);

    return (
        <ButtonStyle 
           onClick={
            () => {
               let numberRandom = getNumberRandom();
               setIdGenerated(numberRandom);

                if (!displayCard) {
                    setDisplayCard(true);
                    setDisplayQuery(false);
                }

              }
            }
        >
            <FontAwesomeIcon icon={faPlus} />

        </ButtonStyle>
    );
}

export {ButtonNew};