import React from 'react';
import { MyContext } from '../MyContext'
import { ButtonNew } from "../ButtonNew";
import { ButtonHistory } from '../ButtonHistory';
import { HistoryCard } from '../HistoryCard';
import { CharacterCard } from '../CharacterCard';
import { Modal } from '../Modal';
import { CardsStyle, H1Style } from '../StyleComponents';
import { QueryCard } from '../QueryCard';



function AppUI() {
  const {
    idGenerated, 
    getNumberRandom,
    openModal,
    currentCharacter,
    displayCard, 
    displayQuery, 

  } = React.useContext(MyContext);

  return (
      <>
      <header>
        <H1Style>Meet Rick and Morty Characters</H1Style>
      </header>

      <main>
        <ButtonNew
          //Between 1 - 826. Because 826 is total of characters.
          //getNumberRandom(inferior, superior)
          getNumberRandom = {() => getNumberRandom(1, 826)}
        />

        <ButtonHistory/>

          {displayCard && (
            <CardsStyle>
              <CharacterCard
                characterId={idGenerated}
                
              />
            </CardsStyle>
          )}

          {(displayQuery && currentCharacter) && (
            <CardsStyle> 
             <QueryCard
              currentCharacter={currentCharacter}
              />
              </CardsStyle>
            )}
        
        {openModal && (
            <Modal>
              <HistoryCard/>
            </Modal>
          )}

      </main>
    </>
  );
}


export {AppUI};
