import { InfoCardStyle, TitleCardStyle, PStyle, ImageCardStyle } from '../StyleComponents';

function QueryCard({currentCharacter}) {
    console.log(currentCharacter)
    
    return (
        <>  

            <ImageCardStyle
                src={currentCharacter.image}
                alt = "Character Rick and Morty"
            />
            <div>
                <TitleCardStyle>
                        <h2>{currentCharacter.name} </h2>
                        <PStyle $label> Character ID: {currentCharacter.id}</PStyle>
                </TitleCardStyle>
                <br></br>
                <InfoCardStyle>
                    <div> 
                        <PStyle $label>Status:</PStyle>
                        <PStyle>{currentCharacter.status}</PStyle>

                        <PStyle $label>Species:</PStyle>
                        <PStyle>{currentCharacter.species}</PStyle>

                        <PStyle $label>Type:</PStyle>
                        <PStyle>{currentCharacter.type}</PStyle>
                    </div>
                    <div>
                        <PStyle $label>Gender:</PStyle>
                        <PStyle>{currentCharacter.gender}</PStyle>
                        <PStyle $label>Origin:</PStyle>
                        <PStyle>{currentCharacter.origin.name}</PStyle>
                        
                        <PStyle $label>Location:</PStyle>
                        <PStyle>{currentCharacter.location.name}</PStyle>
                    </div>
                    
                </InfoCardStyle>
            </div>
        </>
    );
}

export {QueryCard};