import React, { useEffect } from 'react';

// Import everything needed to use the `useQuery` hook
import { useQuery } from '@apollo/client';
import { ApolloQuery } from '../graphql';
import { MyContext } from '../MyContext';

import { InfoCardStyle, TitleCardStyle, PStyle, ImageCardStyle } from '../StyleComponents';

function CharacterCard({characterId}) {
    const {
        addCharacter,
        findElement,
        history,
      } = React.useContext(MyContext);

    const { loading, error, data } = useQuery(ApolloQuery,{
        variables: { characterId }
    });

    useEffect(() => {
        if (data) {
            let isIdHistory = findElement(history, data.character.id);
            console.log('esta idHistory?' + isIdHistory);
            if (isIdHistory == -1) {
                addCharacter(data.character); 
            }
            
        }
    }, [data]);
    
    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error : {error.message}</p>;

    const character = data.character;
    let typeCharacter;
    if (character.type === "") {
        typeCharacter = 'unknown';
    }
    else{
        typeCharacter = character.type;
    }

    return (
        <>
            <ImageCardStyle
                src={character.image}
                alt = "Character Rick and Morty"
            />
            <div>
                <TitleCardStyle>
                        <h2>{character.name} </h2>
                        <PStyle $label> Character ID: {character.id}</PStyle>
                </TitleCardStyle>
                <br></br>
                <InfoCardStyle>
                    <div> 
                        <PStyle $label>Status:</PStyle>
                        <PStyle>{character.status}</PStyle>

                        <PStyle $label>Species:</PStyle>
                        <PStyle>{character.species}</PStyle>

                        <PStyle $label>Type:</PStyle>
                        <PStyle>{typeCharacter}</PStyle>
                    </div>
                    <div>
                        <PStyle $label>Gender:</PStyle>
                        <PStyle>{character.gender}</PStyle>
                        <PStyle $label>Origin:</PStyle>
                        <PStyle>{character.origin.name}</PStyle>
                        
                        <PStyle $label>Location:</PStyle>
                        <PStyle>{character.location.name}</PStyle>
                    </div>
                    
                </InfoCardStyle>
            </div>
        </>
    );
}

export {CharacterCard};