import React from 'react';
import {HistoryCardStyle, TitleCardStyle, PStyle, ImageCardHistoryStyle, ButtonQueryStyle } from '../StyleComponents';
import { MyContext } from '../MyContext';

function HistoryCard() {
    const {
        history, 
        setDisplayCard,
        setDisplayQuery,
        setCurrentCharacter,
        setOpenModal,
        displayCard,
        displayQuery
      } = React.useContext(MyContext);
    return (
        <>
            {history.map((character) => 
                <ButtonQueryStyle
                    key={character.id}
                    onClick={
                        () => {
                            setCurrentCharacter(character);
                            setDisplayCard(false);
                            setDisplayQuery(true);
                            setOpenModal(state => !state);
                        }
                    }
                    
                >
                    <HistoryCardStyle >
                        <ImageCardHistoryStyle
                            src={character.image}
                        />
                        <div>
                            <TitleCardStyle>
                                    <h2 >{character.name} </h2>
                                    <PStyle $label> Character ID: {character.id}</PStyle>
                            </TitleCardStyle> 
                        </div>
                    </HistoryCardStyle>
                </ButtonQueryStyle>
                
            )}
        </>
    );
}

export {HistoryCard};