import React from "react";




const MyContext = React.createContext();

function MyProvider({children}) {

  //State for array to save generate numbers
  const [numbers, setNumbers] = React.useState([]);

  //Number generated for get Character 
  const [idGenerated, setIdGenerated] = React.useState(1);

  //State for Modal
  const [openModal, setOpenModal] = React.useState(false);

  //State for Character's history
  const [history, setHistory] = React.useState([]);

   //State for Current Character
   const [currentCharacter, setCurrentCharacter] = React.useState();

  //State for Display Query Character
  const [displayCard, setDisplayCard] = React.useState(true);
  const [displayQuery, setDisplayQuery] = React.useState(false);

  
  /* Add Character to history */
  function addCharacter (character) {
    let newCharacter = character;
    setCurrentCharacter(newCharacter)
    setHistory( [...history, newCharacter]);
    
    
  }

  function addNumber (number) {
    setNumbers( [...numbers, number]);
    
    
  }

  /*
    Calculate random number between two limits
  */
  function getNumberRandom(inferior, superior ) {
    var numOptions = superior - inferior;
    var random = Math.random() * (numOptions + 1);
    random = Math.floor(random);
    var res = inferior + random;

    if (!numbers.includes(res) ) {    
      addNumber(res);
    }
    else{
      res = res + 1;
    }
    return res;
  }

  const findElement = (array, searchedId) => {
    for (let i = 0; i < array.length; i++) {
      const element = array[i];
      if (element.id === searchedId) {
        return element;
      }
    }
    return -1;
  }



  return (
    <MyContext.Provider value={{
      idGenerated, 
      setIdGenerated,
      getNumberRandom,
      openModal,
      setOpenModal,
      history, 
      addCharacter,
      currentCharacter,
      setCurrentCharacter,
      displayCard, 
      setDisplayCard, 
      displayQuery, 
      setDisplayQuery,
      numbers, 
      setNumbers,
      addNumber,
      findElement
    }}>
      {children}
    </MyContext.Provider>
  );
}


export { MyContext, MyProvider };